import React , {Suspense} from 'react'
import Loading from '../Loading'
let Info = ({info,title}) => {
    return (
       
        <div style={{minWidth: "150px"}} className="d-flex flex-column my-1 my-md-3 flex-grow-1 px-3 text-center text-md-start border-0 border-end"> 
        <p className="text-muted d-block my-2 text-uppercase fs-6 fw-bold">
            {title}
        </p>
        <h4>
 <Suspense fallback={<Loading />}>
            {info}
</Suspense>
        </h4>
        </div>
        
    )
}

export default Info;