import arow from '../../../images/icon-arrow.svg'
import './index.css'
let Searchbar = ({dataGrapper,changeip,url}) => {
 
  return (
  
<div className="w-100 row m-0">
<form className="col-11 col-md-8 col-lg-6 mx-auto">
      <div className="input-group mb-3">
        <input
        onChange={(e)=>changeip(e.target.value)}
        style={{borderRadius : "15px"}}
          type="text"
          className="form-control form-control-lg rounded-end rounder"
          placeholder="Search for an IP address or Domain"
          aria-label="Recipient's username"
          aria-describedby="button-addon2"
          value={url}
        />
        <button
        onClick={dataGrapper}
        style={{borderRadius : "15px"}}
          className="btn btn-outline-secondary bg-dark form-control-lg px-4 rounded-start border-0 rounder"
          type="button"
          id="button-addon2"
        >
         <img src={arow} alt="arrow"/>
        </button>
      </div>
    </form>
</div>
  );
};

export default Searchbar
