import Info from "../Info"
import React , {Suspense} from 'react'
import Loading from '../Loading'
let Infobox = ({myData}) => {
    try {
        return (

            <div style={{borderRadius: "15px",width:"85%"}} className="shadow position-absolute top-100 start-50 translate-middle mx-md-auto px-1 px-md-5 container d-flex flex-wrap flex-md-nowrap justify-content-evenly align-items-center bg-white py-3 py-md-5">
                   <Suspense fallback={<Loading />}>
                   
                  <Info info={myData?myData.ip:null} title="IP ADDRESS"/>
                  </Suspense>
                  <Suspense fallback={<Loading />}>
                  <Info info={myData?`${myData.location.country},${myData.location.city},${myData.location.geonameId}`:null} title="LOCATION"/>
                  </Suspense>
                  <Suspense fallback={<Loading />}>
                  <Info info={myData?`UTC${myData.location.timezone}`:null} title="TIMEZONE"/>
                   </Suspense>
                   <Suspense fallback={<Loading />}>
                  <Info info={myData?myData.isp:null} title="ISP"/>
                   </Suspense>
               
               </div>
           )
    } catch (error) {
        
    }
    return
   
}

export default Infobox