import './index.css'
import Texttitle from '../../components/staticComponent/Texttitle'
import Searchbar from '../../components/dynamicComponent/Searchbar'
import Infobox from '../../components/dynamicComponent/Infobox'
let Headlayout = ({dataGrapper,changeip,url,myData}) => {
    return (
        <div className="container-fluid position-relative p-0 headerSection">
            <Texttitle />
            <Searchbar dataGrapper={dataGrapper} changeip={changeip} url={url}/>
            <Infobox myData={myData}/>
        </div>
    )
}

export default Headlayout;