import "./index.css";
let Maplayout = ({myrData}) => {

 
try {
    if (myrData){var {lat,lng} = myrData.location;}
let api= `https://api.maptiler.com/maps/basic/?key=HGPSwzDQY26yZCdA2ptF#10.1/${lat}/${lng}`
    return (
        <div className="container d-flex justify-content-center align-items-center">
    <iframe style={{height: "90vh",width: "90vw"}} src={api} title="mainMap"></iframe>
        </div>
      );
    
} catch (error) {
    return (
        <div className="container d-flex justify-content-center align-items-center">
            <p className="fw-bolder fs-1 text-danger d-block my-5">Warnning ... Invalid IP Address !!!</p>
        </div>
      );
}

};
export default Maplayout;
