// import m from  '../images/pattern-bg.png'
import { useState } from "react";
import Headlayout from "./headelayout";

import Maplayout from "./maplayout";

let Mainlayout = () => {
  let [url, setUrl] = useState("");
  let [myData, setMyData] = useState();
  let dataGrapper = async () => {
      try {
          
          let data = await fetch(
            `https://geo.ipify.org/api/v2/country,city,vpn?apiKey=at_G9e1DeVCAYDLA0ZGUMUoxz23vertq&ipAddress=${url}`
          );
          let finalData = await data.json();
          
          setMyData(finalData);
      } catch (error) {
return
      }
  };
  let changeip = (e) => {
    setUrl(e);
  };
  return (
    <div className="">
      <Headlayout
        dataGrapper={dataGrapper}
        changeip={changeip}
        url={url}
        myData={myData}
      />
      <Maplayout myrData={myData}/>
    </div>
  );
};

export default Mainlayout;
